@media #{$medium-up} {
    #wrapper {
        section#header {
            .brandbox {
                .brandboxContent {
                    .logininfo {
                        @include grid-row();
                        .userphoto {
                            width: 20%;

                        }
                        .greeting {
                            display: inline-block;
                            border-bottom: 0;
                            h1 {
                            }
                            p {
                                margin-bottom: 1.25rem;
                            }
                        }
                        .accountdetails {
                            width: 35%;
                            display: inline-block;
                            padding-left: 20px;
                        }
                    }
                }
            }
        }
        div#main {
            section {
                .brandbox {
                    #searchbar.scrollCompanion {
                        h1 {
                            @include grid-column(6);
                            font-size: 36px;
                            text-align: left;
                            margin-top: $column-gutter;
                            span {
                                font-size: 36px;
                            }
                        }
                        .wrapper {
                            .formcontainer {
                                .filter {
                                    @include grid-column(6);	
                                    padding-right: 0;
                                    border-top:0;
                                    border-left: 1px solid rgba(255,255,255,0.4);
                                }
                                .text_field, .select_field {
                                    @include grid-column(12);
                                    input {
                                    }
                                    label {
                                        &.active {
                                            font-size: 21px;
                                            left: 17px;
                                        }
                                    }
                                }
                                .select_field {
                                    @include grid-column(6);
                                }
                                .submit_field {
                                    @include grid-column(6);
                                    margin-top: $column-gutter;
                                    float: right;
                                    button {
                                    }
                                }
                            }
                        }
                    }
                }
                div.employer {
                    div.company_overview {
                        div.company_fields {
                            div.fieldsgroup_vacancy {
                                span.bottom {
                                    margin: 0;
                                    margin-right: 20px;
                                    display: inline-block;
                                }
                                .bottom.editing_blocked {
                                    float: right;
                                    margin-right: 0;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

}

@media #{$medium-only} {
}