@media #{$large-up} {

    .sb-slidebar, .sb-slidebar.sb-active {
        display: none;
    }
    #wrapper {
        div#main {
            section {
                .brandbox {
                    #searchbar.scrollCompanion {
                        .wrapper {
                            .formcontainer {
                                .text_field, .select_field {
                                    @include grid-column(8);
                                }
                                .select_field {
                                    @include grid-column(4);
                                    margin-top: -59px;
                                }
                                .submit_field {
                                    @include grid-column(4);
                                }
                                .bottombar {
                                    margin: 0 $column-gutter/2;
                                    width: auto;
                                }
                            }
                        }
                    }
                }
                .wrapper {
                    @include grid-row();
                }
                .sidebar, aside {
                    @include grid-column(4);
                    & + .content, & + section#overview, & + .notice {
                        @include grid-column(8);
                        padding-right: 0;
                    }
                }
                .content {
                    @include grid-column(12);
                }
            }
        }
    }
}
