#wrapper {
        div#main {
                section {
            .overview {
                &.accountmanagement {
                            .wrapper {
                        a.btn {
                            padding-left: 40px;
                            background-color: $main;
                            color: $white;
                            &:before {
                                background-image: none;
                            }
                            &:hover {
                                background-color: lighten($main, 10);
                                color: $white;
                                &:before {
                                    background-image: none;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
