        background-position: 100% 63%;
        background-repeat: no-repeat;
        appearance: none !important;
        overflow : hidden;
        text-indent : 0.01px;
        text-overflow : '';
        background-color: transparent;
        border: 0;
        border-bottom: 1px solid $border;
        border-radius: 0;
        padding: 16px 20px 4px 0;
        height: 3rem;
        &:focus {
            outline: 0;
            border-bottom: 1px solid $main;
        }
        &::-ms-expand {
            display: none;
        }
    }

    select.fancyselect {
        padding: 0 50px 0 15px;
        height: 47px;
        margin: 0;
        border-radius:5px;
        border: none;
        border-bottom: 3px solid darken($select, 10);
        background: $select;
        color: $text;
        font-weight: 900;
        outline: none;
        appearance: none !important;
        text-indent: 0.01px;
        text-overflow: '';
        cursor: pointer;
        margin-bottom: 10px;
    }

    label + select.fancyselect , label + select.fancyselect + .chosen-container{
        margin-top: 10px;
        margin-bottom: 10px;
    }
    /* @group Base */
    .chosen-container {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        zoom: 1;
        *display: inline;
        user-select: none;
        .chosen-drop {
            position: absolute;
            top: -999em;
            left: 0;
            z-index: 1010;
            width: 100%;
            border: 1px solid #aaa;
            border-top: 0;
            background: #fff;
            box-shadow: 0 4px 5px rgba(0, 0, 0, 0.15);
        }
        &.chosen-with-drop .chosen-drop {
            top: 100%;
        }
        a {
            cursor: pointer;
        }
        .chosen-results {
            color: #444;
            position: relative;
            overflow-x: hidden;
            overflow-y: auto;
            margin: 0 4px 4px 0;
            padding: 0 0 0 4px;
            max-height: 240px;
            -webkit-overflow-scrolling: touch;
            li {
                display: none;
                margin: 0;
                padding: 5px 6px;
                list-style: none;
                line-height: 15px;
                word-wrap: break-word;
                -webkit-touch-callout: none;
                &.active-result {
                    display: list-item;
                    cursor: pointer;
                }
                &.disabled-result {
                    display: list-item;
                    color: #ccc;
                    cursor: default;
                }
                &.highlighted {
                    background-color: $main;
                    color: #fff;
                }
                &.no-results {
                    color: #777;
                    display: list-item;
                    background: #f4f4f4;
                }
                &.group-result {
                    display: list-item;
                    font-weight: bold;
                    cursor: default;
                }
                &.group-option {
                    padding-left: 15px;
                }
                em {
                    font-style: normal;
                    text-decoration: underline;
                }
            }
        }
    }

    /* @group Single Chosen */
    .chosen-container-single {
        margin-bottom: rem-calc(16px);
        .chosen-default {

        }
        .chosen-single-with-deselect span {
            margin-right: 38px;
        }
        .chosen-single {
            position: relative;
            display: block;
            overflow: hidden;
            padding: 0 50px 0 15px;
            height: 47px;
            line-height: 47px;
            width: auto;
            margin: 0;
            border-radius:5px;
            border: none;
            border-bottom: 3px solid darken($select, 10);
            background: $select;
            color: $text;
            font-weight: 900;
            outline: none;
            cursor: pointer;
            span {
                display: block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            div {
                position: absolute;
                top: 0;
                right: 15px;
                display: block;
                width: 18px;
                height: 100%;
                b {
                    display: block;
                    width: 100%;
                    height: 100%;
                    background-image: url('data:image/png;base64,');
                    background-repeat: no-repeat;
                    background-position: 97% center;
                    @include transition($transition-time:100ms);
                }
            }
        }
        &.chosen-with-drop {
            .chosen-single {
                div b {
                    @include rotate(180deg);
                    @include transition($transition-time:100ms);
                }
            }
        }
        &.chosen-disabled .chosen-single abbr:hover {
            background-position: -42px -10px;
        }
        .chosen-search {
            position: relative;
            z-index: 1010;
            margin: 0;
            padding: 3px 4px;
            white-space: nowrap;
            input[type="text"] {
                margin: 1px 0;
                padding: 4px 20px 4px 5px;
                width: 100%;
                height: auto;
                outline: 0;
                border: 1px solid #aaa;
                background: $white;
                font-size: 1em;
                font-family: sans-serif;
                line-height: normal;
                border-radius: 0;
            }
        }
        .chosen-drop {
            margin-top: -1px;
            border-radius: 0 0 4px 4px;
            background-clip: padding-box;
        }
        &.chosen-container-single-nosearch .chosen-search {
            position: absolute;
            left: -9999px;
        }
    }

    @media #{$medium-up} {
        .chosen-container {
            width: auto !important;
        }
    }
}

/***************
  Radio Buttons
***************/
#wrapper {
    /* Remove default checkbox */
    [type="radio"]:not(:checked),
        [type="radio"]:checked {
        position: absolute;
        left: -9999px;
    }

    [type="radio"]:not(:checked) + label,
        [type="radio"]:checked + label {
        position: relative;
        padding-left: 35px;
        cursor: pointer;
        display: inline-block;
        margin-left: 0;
        line-height: 30px;

        -webkit-user-select: none; /* webkit (safari, chrome) browsers */
        -moz-user-select: none; /* mozilla browsers */
        -khtml-user-select: none; /* webkit (konqueror) browsers */
        -ms-user-select: none; /* IE10+ */
    }

    [type="radio"] + label:before,
        [type="radio"] + label:after {
        content: '';
        position: absolute;
        left: 0px;
        top: 5px;
        @media #{$medium-up} {
            top: 1px;
        }
        width: 20px;
        height: 20px;
        z-index: 0;
        @include transition(all, .28s, ease);
    }

    /* Unchecked styles */
    [type="radio"]:not(:checked) + label:before {
        border-radius: 50%;
        border: 2px solid $radio-border;
    }
    [type="radio"]:not(:checked) + label:after {
        border-radius: 50%;
        border: 2px solid $radio-border;
        z-index: -1;

        @include transform(scale(0));
    }

    /* Checked styles */
    [type="radio"]:checked + label:before {
        border-radius: 50%;
        border: 2px solid $radio-border;
    }
    [type="radio"]:checked + label:after {
        border-radius: 50%;
        border: 2px solid $radio-checked;
        background-color: $radio-checked;
        z-index: 0;
        @include transform(scale(1.02));
    }


    /* Disabled style */
    [type="radio"]:disabled:not(:checked) + label:before,
        [type="radio"]:disabled:checked + label:before {
        background-color: $disabled-color;
        border-color: $disabled-color;
    }
    [type="radio"]:disabled + label {
        color: $disabled-color;
    }
    [type="radio"]:disabled:not(:checked) + label:hover:before {
        border-color: $disabled-color;
    }
}
/***************
   Checkboxes
***************/
#wrapper {
    /* Remove default checkbox */
    [type="checkbox"]:not(:checked),
        [type="checkbox"]:checked {
        position: absolute;
        left: -9999px;
    }
    [type="checkbox"]:not(:checked) + label,
        [type="checkbox"]:checked + label {
        position: relative;
        padding-left: 35px;
        cursor: pointer;
        display: inline-block;
        line-height: 25px;

        -webkit-user-select: none; /* webkit (safari, chrome) browsers */
        -moz-user-select: none; /* mozilla browsers */
        -khtml-user-select: none; /* webkit (konqueror) browsers */
        -ms-user-select: none; /* IE10+ */
    }

    /* checkbox aspect */
    [type="checkbox"] + label:before {
        content: '';
        position: absolute;
        margin-top: 2px;
        left: 0;
        z-index: 0;
        border-radius: 1px;
        @include transition(all, .28s, ease);
    }
    /* Unchecked styles */
    [type="checkbox"]:not(:checked) + label:before {
        top: 0px;
        width: 20px;
        height: 20px;
        border: 1px solid $checkbox-border;
        border-radius: 5px;
    }

    /* Checked styles */
    [type="checkbox"]:checked + label:before {
        left: -2px;
        top: -1px;
        width: 10px; height: 18px;
        border-top: 2px solid transparent;
        border-left: 2px solid transparent;
        border-right: 2px solid $checkbox-checked;
        border-bottom: 2px solid $checkbox-checked;
        @include transform(rotateZ(40deg));
        -webkit-backface-visibility: hidden;
        -webkit-transform-origin: 20% 40%;
        transform-origin: 100% 100%;
    }

    /* disabled checkbox */
    [type="checkbox"]:disabled:not(:checked) + label:before,
        [type="checkbox"]:disabled:checked + label:before {
        top: 0;
        left: 0;
        box-shadow: none;
        background-color: $disabled-color;
        width: 20px; height: 20px;
        border: 2px solid $disabled-color;
        transform: rotateZ(0deg);
    }
    [type="checkbox"]:disabled + label {
        color: $disabled-color;
    }
    [type="checkbox"]:disabled:not(:checked) + label:hover:before {
        border-color: $disabled-color;
    }
}

