    textarea {
        max-width: 100%;
    }
    .formfield {
        label {
            color: $label;
        }
        &.missing,
        &.incorrect {
            label {
                color: #DF0101;
            }
        }
    }
    .form {
        background: $white;
    }
    .text_field , .password_field, .file_field, .longtext_field {
        position: relative;
        padding-bottom: 11px;
        label {
            color: $label;
            position: absolute;
            top: 1rem;
            left: 0;
            font-size: 1rem;
            @include transition();
            &.active {
                font-size: .8rem;
                @include transform(translateY(-90%));
            }
        }
        input[type="text"]:focus + label,
            input[type="file"]:focus + label,
            input[type="password"]:focus + label,
            input[type="email"]:focus + label,
            input[type="date"]:focus + label,
            textarea:focus + label {
            color: $main;
        }
    }
    .select_field {
        padding-bottom: 11px;
    }
    .readonly {
        input {
            color: $label;
            &~ .bar:before, &~ .bar:after {
                display: none;
            }
            &:hover {
                cursor: not-allowed;
            }
        }
    }

    input[type="date"],input[type="email"],input[type="password"],input[type="file"],textarea,input[type="text"] {
        background-color: transparent;
        border: none;
        outline: none;
        height: 3rem;
        width: 100%;
        font-size: 1rem;
        float: none;
        box-shadow: none;
        @include transition($transition-time:.3s);
        padding-top: 13px;
        /* active state */
        &:focus ~ .bar:before, &:focus ~ .bar:after {
            width: 50%;
        }
    }
    
    /* Splitted from above because of compiling issue */
    input[type="date"],input[type="email"],input[type="password"],input[type="file"],textarea,input[type="text"] {
        border-bottom: 1px solid $border;
    }

    input[type="file"] {
        height: 2rem;
        margin-top: 2rem;
        border-bottom: none;
    }

    textarea {
        overflow-y: hidden; /* prevents scroll bar flash */
        padding-top: 1.6rem; /* prevents text jump on Enter keypress */
        resize: none;
        margin-bottom: -4px;
        box-sizing: content-box;
    }

    // For textarea autoresize
    .hiddendiv {
        display: none;
        white-space: pre-wrap;
        word-wrap: break-word;
        overflow-wrap: break-word; /* future version of deprecated 'word-wrap' */
        padding-top: 1.6rem; /* prevents text jump on Enter keypress */
        box-sizing: content-box;
    }

    .bar {
        position: relative;
        display: block;
        width: 100%;
        &:before, &:after {
            content: '';
            height: 2px;
            width: 0;
            bottom: 0px;
            position: absolute;
            background: $main;
            @include transition();
        }
        &:before {
            left: 50%;
        }
        &:after {
            right: 50%;
        }
    }

    section form .formfield.file_field {
        .bar {
            display: none;
        }
        .boolean_field {
            position: relative;
            margin: $column-gutter 0;
            img {
                margin-bottom: $column-gutter/2;
            }
            a {
                display: block;
                &.download {
                    width: auto;
                    @include icon(download){
                        margin-right: 10px;
                    }
                    display: inline-block;
                }
            }
            .image_delete_and_download {
                padding-left: $column-gutter;
                label {
                    top: inherit;
                    margin-bottom: $column-gutter/2;
                    margin-right: $column-gutter;
                }
            }
        }
    }

}
