                                        padding-left: 0;
                                        width: auto;
                                        display: inline-block;
                                        input,
                                        label {
                                            width: auto;
                                            border: none;
                                        }
                                        label {
                                            line-height: inherit;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media #{$large-up} {
    #wrapper {
        div#main {
            section {
                .formcontainer form[class$="_form"]{
                    .formfield {
                        &.daysfield.check_field {
                            width: 14%;
                        }
                    }
                }
            }
        }
    }
}