.searchform_form {
    width: 100%;
    position: absolute;
    left: 0;
    top: 77px;
    height: 0;
    overflow: hidden;
    padding: 0 20px;
    opacity: 0;
    @include transition();
    background: $submenu;
    box-shadow: inset 0px 5px 16px rgba(0,0,0,0.2);
    text-align: center;
    &.active {
        height: auto;
        opacity: 1;
        padding: 20px;
        overflow: inherit;
        @include transition();
    }
    .formfield {
        display: inline-block;
        &.text_field {
            width: 50%;
            padding-bottom: 0;
            input {
                height: 4rem;
                font-size: 1.5rem;
                color: $white;
                &::-webkit-input-placeholder {
                    color: $white;
                }
                &:-moz-placeholder {
                    color: $white;
                    opacity:  1;
                }
                &::-moz-placeholder {
                    color: $white;
                    opacity:  1;
                }
                &:-ms-input-placeholder {
                    color: $white;
                }
            }
            span.bar {
                border-bottom: 1px solid $white;
            }
        }
        &.submit_field {
            button {
                border: 0;
                padding: 9px 14px;
                text-transform: lowercase;
                line-height: 32px;
            }
        }
    }
}

@media #{$medium-up} {
    .searchform_form {
        .formfield {
            &.submit_field {
                button {
                    padding: 9px 14px 9px 31px;
                    overflow: inherit;
                    @include icon("search") {
                        position: absolute; 
                        margin-left: -22px;
                    };	
                }
            }
        }
    }
}

@media #{$large-up} {
    .searchform_form {
        top: inherit;
    }
}