.socialbookmarks a {
    margin-left: 6px;
}

.emptycontentblockcontent {
    display: none;
}

div.contentblock.offline {
    opacity: 0.2;
    filter: alpha(opacity=20);
    zoom: 1;
}

.bannerimage {
    padding-bottom: 25px;
    border-top: 1px solid $border;
    padding-top: 10px;

    .bannerexample,
    .bannercode {
        display: inline-block;
        width: 50%;
        vertical-align: top;
    }

    .bannercode {
        padding-top: 40px;
        textarea {
            padding: 5px;
            min-height: 150px;
            border: 1px solid $border;
            margin-bottom: 10px;
        }
    }
}

.contactform {
    .compositeform {
        padding-top: 20px;
        padding-bottom: 20px;
        padding-left: 15px;
        p {
            color: $text;
        }
    }
}

.article_wrapper {
    @include grid-row();
}

.article-contentblocks {
    &.article-contentblocks {
        @include grid-row();
        position: relative;

        .contentblock {
            margin: 50px 0;
            @include clearfix();
        }

        .articlemaincontentblocks {
            @include grid-column(12);
            .title {
                text-transform: uppercase;
                h2 {
                    margin-bottom: 33px;
                }
            }
            .richtext {
                line-height: 34px;
                h2 {
                    text-transform: uppercase;
                    margin-bottom: 33px;
                }
                h3,h4 {
                    text-transform: uppercase;
                    margin-bottom: 17px;
                }
                p {
                    line-height: 34px;
                }
                li {
                    line-height: 34px;
                }
                table {
                    width: 100%;
                    tr td {
                        padding: $column-gutter/2;
                        vertical-align: top;
                        border: 1px solid $border;
                    }
                }
            }
            .photolabel {
                font-style: italic;
                font-size: 15px;
                line-height: 34px;
            }

            .collectionindex {
                ul {
                    list-style-type: none;
                    margin: 0;
                    li {
                        display: block;
                        a {
                            color: $text;
                            font-weight: 700;
                            padding: 10px 20px;
                            border: 1px solid $border;
                            box-shadow: inset 0px -2px rgba(147, 137, 137, 0.1);
                            background: lighten($border,8);
                            display: inline-block;
                            font-size: 15px;
                            width: 100%;
                            text-align: center;
                        }
                    }
                }
            }
            .photophoto {
                .photocontainer {
                    @include grid-column(6);
                    padding-left: 0;
                }
                .photocontainer_first {
                    padding-left: 0px;
                }
            }

            .textphoto {
                .richtext {
                    @include grid-column(6);
                    padding-left: 0;
                }
                .richtext_first {
                    padding-left: 0px;
                }
                .photolabel {
                    text-align: right;
                }
            }

            .phototext, .textphoto {
                .photocontainer {
                    @include grid-column(12);
                }
                .richtext {
                    @include grid-column(12);
                }
            }

            .textphoto .richtext,
            .phototext .photocontainer {
                padding: 0px;
            }

            .textphoto .photocontainer,
            .phototext .richtext {
                padding: 0px;
            }
        }
    }
    &.article-contentblocks-without-banners {
        .bannerscontentblocks {
            width: 0%;
            position: absolute;
            top: 0px;
            right: 0px;
        }
    }
    &.article-contentblocks-with-banners {
        .articlemaincontentblocks {
            @include grid-column(12);
        }
        .bannerscontentblocks {
            @include grid-column(12);
            .contentblock {
                @include card;
            }
        }
    }
}

.aeroplane_sitemaincontent .article-contentblocks.article-contentblocks-without-banners .bannerscontentblocks {
    min-height: 500px;
}

#wrapper #main .contentblock {
    position: relative;
    a.collectionindexanchor {
        position: absolute;
        left: 0px;
        top: 0px;
    }
    a.collectionindextoindex {
        display: block;
        clear: both;
        &:hover {
            text-decoration: underline;
        }
    }
    blockquote {
        font-size: 1.2rem;
        font-style: italic;
        margin: 0;
        @include icon(quote){
            background: $link;
            color: $white;
            font-size: 2rem;
            padding: 4px 10px;
            margin-left: -60px;
            vertical-align: middle;
            margin-right: 20px;
        };
    }
    .module {
        padding: 0;
        a {
            display: block;
            border-bottom: 1px solid $border;
            padding: $column-gutter/2;
            @include clearfix();
            &:hover{
                background: $filterBG;
                @include transition();
            }
            img {
                display: block;
                margin-right: $column-gutter/2;
            }
            section {
                display: block;
                vertical-align: top;
                color: $text;
                 h2 {
                    font-size: 14px;
                    color: $text;
                    text-transform: inherit;
                }
                p {
                    font-size: 14px;
                    margin-bottom: 0;
                }
            }
        }
        & + .link {
            text-align: right;
        }
        &.layout-overview {
            img {
                float: left;
            }
            a section{
                float: left;
                width: 100%;
            }
        }
        &.layout-tiles {
            a {
                display: block;
                background: $main;
                color: $white;
                padding: $column-gutter $column-gutter/2;
                text-align: center;
                border-bottom: 1px solid $border;
                position: relative;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                vertical-align: top;
                &:after {
                    width: 0;
                    height: 0;
                    border-style: solid;
                    border-width: 0 0 30px 30px;
                    border-color: transparent transparent lighten($main,10) transparent;
                    content: "";
                    position: absolute;
                    pointer-events: none;
                    right: 0;
                    bottom: 0;
                }
                &:hover {
                    background-color: lighten($main,10);
                }
                section {
                    h2 {
                        margin: 0;
                        color: $white;
                        text-transform: uppercase;
                        font-weight: 500;
                        font-size: 16px;
                    }
                }
            }
            & + .link {
                background: $main;
                color: $white;
                border-bottom: 5px solid darken($main,10);
                a {
                    color: $white;
                }
            }
        }
        &.layout-list {
            h2 {
                @include icon(arrow-right){
                    border-radius: 50%;
                    background-color: $link;
                    color: $white;
                    font-size: 14px;
                    margin-right: 15px;
                    vertical-align: bottom;
                }
            }
        }
        &.layout_calendar {
            & + .link {
                background: $main;
                a {
                    color: $white;
                }
            }
            .calendar-monthpager {
                background: $main;
                @include grid-row();
                border-bottom: 1px solid lighten($main,20);
                a {
                    width: 20%;
                    float: left;
                    color: lighten($main,20);
                    text-transform: lowercase;
                    border: 0;
                    padding: 0;
                    line-height: 66px;
                    font-size: 0rem;
                    @include transition;
                    &:before {
                        @include transition;
                        display: inline-block;
                        font-size: 3rem;
                        float: left;
                        color: darken($main,40);
                    }
                    &:hover {
                        background: darken($main,10);
                        color: $white;
                        &:before {
                            @include transition;
                            color: $white;
                        }
                    }
                }
                .calendar-prevmonth {
                    @include icon(arrow-left);
                }
                .calendar-nextmonth {
                    @include icon(arrow-right);
                    text-align: right;
                    &:before {
                        float: right;
                    }
                }
                .calendar-currentmonth {
                    width: 60%;
                    float: left;
                    line-height: 66px;
                    margin: 0;
                    font-weight: 500;
                    color: $white;
                    text-transform: capitalize;
                    text-align: center;
                }
            }
            .calender-content {
                ul {
                    margin: 0;
                    list-style: none;
                    li {
                        a {
                            background: $main;
                            color: $white;
                            border-bottom: 1px solid lighten($main,20);
                            position: relative;
                            &.active {
                                .overlay {
                                    display: block;
                                }
                            }
                            h3 {
                                margin: 0;
                                color: $white;
                            }
                            p {
                                margin-bottom: 0;
                            }
                            .overlay {
                                position: absolute;
                                top: 0;
                                left: 0;
                                background-color: rgba(0, 0, 0, 0.6);
                                width: 100%;
                                height: 100%;
                                display: none;
                                text-align: right;
                                .action {
                                    text-align: center;
                                    height: 100%;
                                    display: inline-block;
                                    padding: 0 30px;
                                    border-left: 1px solid $main;
                                    vertical-align: top;
                                    max-width: 50%;
                                    position: relative;
                                    &:hover, &.active{
                                        background-color: rgba(0, 0, 0, 0.6);
                                    }
                                }
                                .share-channels {
                                    font-size: 0rem;
                                    position: relative;
                                    &:before {
                                        font-size: 2rem;
                                        position: absolute;
                                        top: 50%;
                                        left: 50%;
                                        transform: translate(-50%,-50%);
                                    }
                                    &.facebook {
                                        @include icon(facebook);
                                    }
                                    &.twitter {
                                        @include icon(twitter);
                                    }
                                }
                                .more span{
                                    transform: translateY(-50%);
                                    top: 50%;
                                    position: relative;
                                    display: block;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
.articles_home .article-contentblocks .articlemaincontentblocks {
    padding: 0;
}
.articles_home, .rij2_wrapper{
    overflow: auto;
    .contentblocks {
        padding: 0;
        margin-top: $column-gutter;
        .contentblock {
            @include grid-column(12);
            margin: 0;
            margin-bottom: $column-gutter; 
            font-size: rem-calc(15px);
            p {
                font-size: rem-calc(15px);
                line-height: inherit;
            }
            a {
                color: $link;
                background-color: inherit; 
            }
            .contentblockcontent {
                &.title {
                    border: none;
                    margin-bottom: 0;
                    padding: 0;
                    background: none;
                    box-shadow: none;
                    text-transform: inherit;
                    h2 {
                        background: $main;
                        color: $white;
                        margin: 0;
                        padding: $column-gutter/4 $column-gutter/2;
                        font-size: 1.1rem;
                        a {
                            color: $white;
                        }
                        span {
                            font-weight: 500;
                            &:before {
                                content: '|';
                                display: inline-block;
                                margin: 0 10px;
                            }
                        }
                    }
                }
                &.richtext {
                    padding: $column-gutter/2;
                    line-height: inherit;
                }
                &.photo {
                    padding: 0;
                    img {
                        object-fit: cover;
                        min-width: 100%;
                        min-height: 100%;
                    }
                }
                @include card;
                border: 0px;
                margin-bottom: 0;
                padding: 0; 
                .emptycontentblockcontent {
                    display: none;
                }
                &.label a {
                    font-size: rem-calc(15px);
                    background: $filterBG;
                    color: $main;
                    font-weight: 700;
                    padding: $column-gutter/2;
                    display: block;
                    border-top: 1px solid $border;
                    @include icon(arrow){
                        float: right;
                        font-size: 32px;
                        font-weight: 700;
                    };
                    &:hover {
                        background: darken($filterBG,3);
                    }
                }
            }
            &.bannerimagelabel {
                .image {
                    flex: 1;
                }
            }
            &.bannertextimage {
                .richtext {
                    flex: 1;
                }
            }  
            &.servicepoints {
                #map-canvas {
                    min-height: 383px;
                    flex: 1;
                    margin: 0px;
                    padding: 0px;
                    margin: (-$column-gutter/2) (-$column-gutter/2) $column-gutter/2;
                    img {
                          max-width: none;
                    }
                    .noscrollbar {
                        line-height:1.35;
                        overflow:hidden;
                        white-space:nowrap;
                    }
                }
            }
            &.bannerimagetextlabel {
                .contentblockcontent {
                    background: $link;
                    color: $white;
                    h2, h3 {
                        background: $link;
                        color: $white;
                    }
                }
            }
            &.photophotophotophoto {
                .contentblockcontent {
                    border: 0;
                }
            }
            .itemscontainer, .vacancy_latest_wrapper {
                @include grid-row();
                @include card;
                padding: 0;
                margin-bottom: 0; 
                a {
                    display: block;
                    background: $white;
                    padding: $column-gutter/2;
                    border-bottom: 1px solid $border;
                    color: $text;
                    @include transition();
                    &:hover{
                        background: $filterBG;
                        @include transition();
                    }
                    .row {
                        @include clearfix;
                        .header {
                            font-weight: 700;
                        }
                        img {
                            float: left;
                            margin-right: $column-gutter/2;
                        }
                        section {
                            h2 {
                                font-size: rem-calc(15px);
                                color: $text;
                                text-transform: inherit;
                                span:before {
                                    content: '|';
                                    display: inline-block;
                                    margin: 0 10px;
                                }
                            }
                            p {
                                font-size: rem-calc(15px);
                            }
                        }
                    }
                    &.btn {
                        font-size: rem-calc(15px);
                        background: $filterBG;
                        color: $main;
                        font-weight: 700;
                        @include icon(arrow){
                            float: right;
                            font-size: 32px;
                            font-weight: 700;
                        };
                        &:hover {
                            background: darken($filterBG,3);
                        }
                    }
                }
                .label {
                    margin-bottom: 0;
                    padding: 0;
                }
            }
            .vacancycontainer {
                .itemscontainer {
                    a:nth-child(4), a:nth-child(5) {
                        display: none;
                    }
                }
            }
        }
    }
}
.profile_completeness {
    .bar {
        text-align: center;
    }
    .progress {
        position: relative;
        display: inline-block;
        line-height: 1.2;
    }
    .progresstext {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 2rem;
    }
    .proposal {
        a {
            @include btn;
            font-size: 14px;
        }
    }
}


@media #{$medium-up} {
    .articles_home, .rij2_wrapper{
            .contentblocks {
                .contentblock {
                    @include grid-column(6);
                }
            }

    }
    .article-contentblocks {
        &.article-contentblocks {
            .articlemaincontentblocks {
                .collectionindex {
                    ul {
                        li {
                            display: inline-block;
                            a {
                                width: auto;
                            }
                        }
                    }
                }
                .texttext {
                    .richtext {
                        @include grid-column(6);
                        padding-left: 0;
                    }
                    .richtext_first {
                        padding-left: 0px;
                    }
                }
                .phototext, .textphoto {
                    .photocontainer {
                        @include grid-column(5);
                    }
                    .richtext {
                        @include grid-column(7);
                    }
                }
                .textphoto .richtext,
                .phototext .photocontainer {
                    padding-left: 0px;
                }

                .textphoto .photocontainer,
                .phototext .richtext {
                    padding-right: 0px;
                }
                .textphoto .photocontainer {
                    float: right;
                    text-align: right;
                }
            }
        }
        &.article-contentblocks-with-banners {
            .articlemaincontentblocks {
                @include grid-column(8);
            }
            .bannerscontentblocks {
                @include grid-column(4);
            }
        }
    }
    #wrapper #main .contentblock {
        .module {
            &.layout-overview {
                a {
                    section {
                        width: 66%;
                    }
                }
            }
            &.layout-blocks {
                @include grid-row();
                .item {
                    @include grid-column(6);
                }
            }
        }
    }
}

@media #{$large-up} {
    .articles_home, .rij2_wrapper{
        .contentblocks {
            .contentblock {
                @include grid-column(4);
                &.bannerimagetextlabel {
                    display: table-row;   
                    .photo, .richtext {
                        width: 50%;
                        flex-flow: inherit;
                        float: none;
                        vertical-align: top;
                        display: table-cell;
                    }
                }
            }
        }
    }

    .articlemaincontentblocks {
        .collectionindex.sticky-scrolled {
            .collectionindex-container {
                position: fixed;
                width: 100%;
                left: 0;
                top: 111px;
                text-align: center;
                border: 1px solid $border;
                box-shadow: inset 0px -2px rgba(147, 137, 137, 0.1);
                background: lighten($border,8);
                z-index: 99;
            }
        }
    }

    #wrapper #main .contentblock {
        a.collectionindexanchor {
            top: -180px;
        }
        .module {
            &.layout-blocks {
                .item {
                   @include grid-column(4);
                   padding: 0 0.95rem;
                }
            }
            &.layout-overview {
                a {
                    section {
                       width: 80%;
                    }
                }
            }
            &.layout-tiles {
                a {
                    width: 50%;
                    display: inline-block;
                    &:nth-child(2n+1){
                        border-right: 1px solid $border;
                    }
                }
            }
            &.layout_calendar {
                .calendar-monthpager {
                    a {
                        width: 31%;
                        font-size: 0.7rem;
                    }
                    .calendar-currentmonth {
                        width: 38%;
                    }
                }
            }
        }
    }
}   
