#wrapper.dashboard-container {
    div#main {
        section {
            .articles.articles_dashboard {
                .article {
                    .articlemaincontentblocks > .contentblock {
                        &.profile_completeness_contentblock {
                            margin: 50px 0;
                            padding-bottom: 0;
                            div.contentblockcontent{
                                &.title { 
                                    background-color: $main;
                                    color: $white;
                                    h2 {
                                        color: $white;
                                    }
                                }
                                &.richtext {
                                    padding: $column-gutter/2 $column-gutter;
                                    border-bottom: 1px solid $border;
                                    background: lighten($border,6);
                                    p {
                                        margin: 0;
                                    }
                                }
                            }
                            .progress {
                                margin: $column-gutter 0;
                            }
                            .proposal {
                                padding: $column-gutter;
                                margin: 0;
                            }
                        }   
                        &.quickstart {
                            overflow: auto;
                            margin-bottom: $column-gutter;
                            padding: 0;
                            overflow: hidden;
                            a {
                                width: 100%;
                                height: 100%;
                                position: relative;
                                display: inline-block;
                                padding: 0;
                                color: $text;
                                background: $border;
                                height: 400px;
                                &:after {
                                    transition: top 1s cubic-bezier(0.455,.03,.515,.955);
                                    content: "";
                                    position: absolute;
                                    left: 0;
                                    top: 0;
                                    right: 0;
                                    bottom: -5px;
                                    background: -moz-linear-gradient(top,transparent 0,rgba(0,0,0,0.65) 100%);
                                    background: -webkit-gradient(linear,left top,left bottom,color-stop(0%,transparent),color-stop(100%,rgba(0,0,0,0.65)));
                                    background: -webkit-linear-gradient(top,transparent 0,rgba(0,0,0,0.65) 100%);
                                    background: -o-linear-gradient(top,transparent 0,rgba(0,0,0,0.65) 100%);
                                    background: -ms-linear-gradient(top,transparent 0,rgba(0,0,0,0.65) 100%);
                                    background: linear-gradient(to bottom,transparent 0,rgba(0,0,0,0.65) 100%);
                                    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000',endColorstr='#a6000000',GradientType=0);
                                    opacity: .75;
                                }
                            }
                            div.title {
                                position: relative;
                                z-index: 1;
                                display: block;
                                text-transform: none;
                                h2 {
                                    padding-top: $column-gutter/2;
                                    color: $white;
                                    text-shadow: 1px 1px 3px rgba(0,0,0,0.3);
                                }
                            }
                            div.richtext {
                                position: absolute;
                                z-index: 1;
                                font-weight: 700;
                                color: #808088;
                                background: $white;
                                width: 100%;
                                color: $text;
                                bottom: 0;
                                padding-top: $column-gutter/2;
                                padding-bottom: $column-gutter/2;
                                @include transition($transition-time:600ms);
                                @include icon(arrow-right){
                                    font-size: 37px;
                                    position: absolute;
                                    top: 50%;
                                    right: 0;
                                    transform: translateY(-50%);
                                    color: #C1C2C4;
                                    transition: color ease-in-out 400ms;
                                }
                                p {
                                    color:$text;
                                    margin-bottom: 0;
                                    padding-bottom: 0;
                                }
                            }
                            div.photo {
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                background: linear-gradient(to bottom,transparent 0,rgba(0,0,0,0.65) 100%);
                                transition: transform 3s cubic-bezier(0.455,.03,.515,.955);
                                img {
                                    object-fit: cover;
                                    min-width: 100%;
                                    min-height: 100%;
                                    position: absolute;
                                    top: 50%;
                                    left: 50%;
                                    transform: translate(-50%,-50%);
                                }
                            }
                            &:hover {
                                a:after {
                                    transition-delay: .15s;
                                    top: -100%;
                                }
                                div.photo{
                                    @include scale(1.1);
                                    transition-delay: .15s;
                                }
                                div.richtext{
                                    background: none;
                                    color: $white;
                                    font-weight: 700;
                                    text-shadow: 1px 1px 3px rgba(0,0,0,0.3);
                                    p {
                                        color: $white;
                                        font-weight: 700;
                                    }
                                    &:before {
                                        color: $main;
                                        @include transition($transition-time:400ms);
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media #{$medium-up} {
    #wrapper.dashboard-container {
        div#main {
            section {
                .articles.articles_dashboard {
                    .article {
                        .articlemaincontentblocks > .contentblock {
                            &.quickstart{
                                width: 48.5%;
                                margin-right: 1.5%;
                                margin-bottom: $column-gutter;
                                float: left;
                                &:nth-last-child(2n+1){
                                    margin-left: 1.5%;
                                    margin-right: 0;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media #{$large-up} {
    #wrapper.dashboard-container {
        div#main {
            section {
                .articles.articles_dashboard {
                    .article {
                        .articlemaincontentblocks > .contentblock {
                            &.profile_completeness_contentblock {
                                overflow: auto;
                                .progress {
                                    float: left;
                                }
                                .proposal {
                                    overflow: hidden;
                                    border-left: 1px solid $border;
                                }
                            }
                            &.quickstart{
                                width: 32.3%;
                                margin-left: 0;
                                margin-right: 0;
                                &:nth-last-child(2n+1){
                                    margin-left: 0;
                                    margin-right: 0;
                                }
                                &:nth-last-child(3n+2){
                                    margin-left: 1.5%;
                                    margin-right: 1.5%;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

// Old dashboard for BC purposes
#wrapper {
    #main {
        &.dashboard-main {
            padding-bottom: 0;
            section {
                .articles {
                    background-color: transparent;
                    padding-bottom: 25px;
                    .article_wrapper {
                        padding-bottom: 0;
                        padding-top: 0;
                    }
                }
            }
        }
        section {
            div.dashboard_overview {
                @include grid-row();
                padding-top: 36px;
                div.wrapper {
                    div.sidebar {
                        @include grid-column(12);
                        div.panel {
                            h2 {
                                font-size: rem-calc(18px);
                            }
                        }
                    }
                    div.content {
                        @include grid-column(12);
                        .offline {
                            display: none;
                        }
                    }
                }
            }
            .rij1_wrapper {
                div.rij1contentblocks.contentblocks {
                    .contentblock.text {
                        display: none;
                    }
                }
            }
            .wrapper {
                &.employers {
                    padding-top: 100px;
                }
                div.rij2contentblocks {
                    div.contentblock {
                        &.photophotophotophoto {
                            div.photocontainer {
                                position: relative;
                                padding: 0;
                                margin-bottom: $column-gutter/2;
                                a {
                                    display: block;
                                    width: 100%;
                                    height: 100%;
                                    overflow: hidden;
                                }
                                p {
                                    position: absolute;
                                    left: $column-gutter/2;
                                    right: $column-gutter/2;
                                    top: $column-gutter/2;
                                    font-size: 1.4rem;
                                    color: $white;
                                    text-shadow: 0 0 3px #a1a1a1;
                                    line-height: 1.2em;
                                }
                            }
                        }
                        &.news {
                            @media screen and (max-width: 435px) {
                                height: 313px;
                            }
                            .newscontainer {
                                position: relative;
                                height: 100%;
                                background: $white;
                                border-bottom: 1px solid #DEDEDE;
                                .contentblockcontent.title {
                                    border-left: 1px solid #DEDEDE;
                                }
                                .first_article {
                                    float: left;
                                    height: 176px;
                                    padding: 20px;
                                    overflow: hidden;
                                    border-left: 1px solid #DEDEDE;
                                    border-right: 1px solid #DEDEDE;
                                    color: $text;
                                    @media screen and (max-width: 435px) {
                                        height: 150px;
                                    }
                                    .photo {
                                        position: relative;
                                        float: left;
                                        width: 90px;
                                        height: 70px;
                                        overflow: hidden;
                                        margin: 0 20px 10px 0;
                                        img{
                                            position: absolute;
                                            width: 100%;
                                            left: 50%;
                                            top: 50%;
                                            @include translate(-50%,-50%);
                                        }
                                    }
                                    .news_intro_publish {
                                        color: $main;
                                        font-family: $body-font-family;
                                        font-size: 12px;
                                        &:before {
                                            float: left;
                                            content: "";
                                            @include icon("clipboard");
                                            margin-right: 8px;
                                        }
                                    }
                                    .news_intro_title {
                                        font-weight: bold;
                                        padding-top: 3px;
                                    }
                                    .contentblockcontent {
                                        float: left;
                                        clear: left;
                                        p {
                                            margin: 0;
                                        }
                                    }
                                }
                                .article_links {
                                    float: left;
                                    .article_link {
                                        display: block;
                                        float: left;
                                        width: 100%;
                                        height: 40px;
                                        border: 1px solid #DEDEDE;
                                        border-style: solid none none solid;
                                        overflow: hidden;
                                        .news_title {
                                            float:left;
                                            line-height: 40px;
                                            padding: 0 15px;
                                            color: $text;
                                            font-weight: bold;
                                            max-width: 89%;
                                        }
                                        .arrow {
                                            position: relative;
                                            float: right;
                                            height: 100%;
                                            width: 30px;
                                            background: #DEDEDE;
                                            .arrow_position {
                                                position: absolute;
                                                @include icon("arrow");
                                                top: 50%;
                                                left: 50%;
                                                @include translate(-50%,-50%);
                                                overflow: hidden;
                                                text-indent: 100px;
                                            }
                                        }
                                        &:hover {
                                            .arrow {
                                                background: $main;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media #{$medium-up} {
    #wrapper {
        div#main {
            section {
                div.dashboard_overview {
                    div.wrapper {
                        div.sidebar {
                            @include grid-column(4);
                        }
                        div.content {
                            @include grid-column(8);
                            .offline {
                                display: none;
                            }
                        }
                    }
                }
                .rij1_wrapper {
                    div.rij1contentblocks.contentblocks {
                        .profile_completeness {
                            > div.richtext {
                                display: inline-block;
                                vertical-align: top;
                                width: 70%;
                                padding-left: 6%;
                            }
                        }
                    }
                }
                div.rij2contentblocks {
                    div.contentblock {
                        &.photophotophotophoto {
                            div.photocontainer {
                                @include grid-column(6);
                                p {
                                    font-size: 1.2rem;
                                }
                                &.odd {
                                    padding-right: $column-gutter/4;
                                    p {
                                        right: $column-gutter;
                                    }
                                }
                                &.even {
                                    padding-left: $column-gutter/4; 
                                    p {
                                        left: $column-gutter;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media #{$large-up} { 
    #wrapper {
        div#main {
            section {
                div.rij2contentblocks {
                    @include grid-row();
                    margin-top: 50px;
                    div.oddcb {
                        @include grid-column(5);
                    }
                    div.evencb {
                        @include grid-column(7);
                    }
                    div.contentblock {
                        &.photophotophotophoto {
                            div.photocontainer {
                                p {
                                    font-size: 1.4rem;
                                }
                            }
                        }
                    }
                    .news {
                        .title {
                            .news_title {
                                width: 100%;
                                height: 100%;
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }
}