#sb-site, .sb-site-container, .sb-slidebar {
    margin: 0;
    padding: 0;
}

html, body {
    width: 100%;
}

html {
    height: 100%; 
}

body {
    min-height: 100%;
    height: auto;
    position: relative; 
    overflow-x: hidden;
}

html.sb-scroll-lock.sb-active:not(.sb-static) {
    overflow: hidden;
}

#sb-site, .sb-site-container {
    width: 100%;
    position: relative;
    z-index: 1; 
}

.sb-slidebar {
    height: 100%;
    overflow-y: auto; 
    position: fixed;
    top: 77px;
    padding-bottom: 77px;
    @media #{$large-up} {  
        top: 95px;
    }
    z-index: 0; 
    display: none; 
    background-color: $main; 
    color: $white;
    -webkit-transform: translate(0px); 
    a, span.username {
        color: $white;
        display: block;
        padding: 20px;
        border-bottom: 1px solid lighten($main,10);
        font-weight: bold;
        font-size: 18px;
    }
    .actions span.account {
        display: inline-block;
        width: 35px;
        height: 35px;
        border-radius: 50%;
        vertical-align: middle;
    }
    span.name {
        margin-left: 10px;
    }
    span.username {
        font-weight: normal;
        font-size: 15px;
        background: #3F3F3F;
    }
    .languageswitcher {
        ul {
            margin: 0;
            li {
                display: inline-block;
                border-left: 1px solid lighten($main,10);
                &:first-child {
                    border-left: 0;
                }
                a {
                    font-weight: normal;
                    background-color: darken($main,7);
                    color: rgba($white,0.8);
                    &.active {
                        font-weight: bold;
                        background-color: inherit;
                        color: $white;
                    }
                }
            }
            //count number of li's and devide the space
            @for $i from 1 through 4 {
                li:first-child:nth-last-child(#{$i}),
                li:first-child:nth-last-child(#{$i}) ~ li {
                    width: #{100%/$i};
                } 
            } 
        }
    }
    nav {
        ul {
            list-style: none;
            margin: 0;
            li {
                &.hassub {
                    & > a {
                        position: relative;
                        &:after {
                            content: '';
                            display: inline-block;
                            width: 0;
                            height: 0;
                            border-style: solid;
                            border-width: 10px 7.5px 0 7.5px;
                            border-color: lighten($main,30) transparent transparent transparent;
                            position: absolute;
                            right: 20px;
                            top: 50%;
                            transform: translate(0,-50%);
                        }
                    }
                    &.active {
                        & > .submenu {
                            display: block !important;
                        }
                        a:after { 
                            transform: translate(0,-50%) rotate(180deg);
                        }
                    }
                }
                &.search {
                    display: none;
                }
                .submenu {
                    display: none;
                    ul {
                        li {
                            &.hassub {
                                & > a {
                                    position: relative;
                                    &:after {
                                        content: '';
                                        display: inline-block;
                                        width: 0;
                                        height: 0;
                                        border-style: solid;
                                        border-width: 10px 7.5px 0 7.5px;
                                        border-color: lighten($main,30) transparent transparent transparent;
                                        position: absolute;
                                        right: 20px;
                                        top: 50%;
                                        transform: translate(0,-50%);
                                    }
                                }
                                &.active a:after { 
                                    transform: translate(0,-50%) rotate(180deg);
                                }
                            }
                            a {
                                background: darken($main, 10);
                                border-bottom: 1px solid lighten($submenu,10);
                                font-weight: normal;
                                font-size: inherit;
                                padding-left: 30px;
                            }
                            &.active {
                                a {
                                    font-weight: 700;
                                }
                            }
                            ul {
                                li {
                                    a {
                                        background: darken($main, 15);
                                        border-bottom: 1px solid lighten($submenu,10);
                                        padding-left: 40px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.sb-left {
    left: 0; 
}

.sb-right {
    right: 0; 
}

html.sb-static .sb-slidebar,
.sb-slidebar.sb-static {
    position: absolute; 
}

.sb-slidebar.sb-active {
    display: block; 
    @media #{$large-up} {  
        display: none;
    }
}

.sb-style-overlay {
    z-index: 9999; 
}

.sb-momentum-scrolling {
    -webkit-overflow-scrolling: touch; 
}


.sb-slidebar {
    width: 100%;
}
 

.sb-slide, #sb-site, .sb-site-container, .sb-slidebar {
    -webkit-transition: -webkit-transform 400ms ease;
       -moz-transition: -moz-transform 400ms ease;
         -o-transition: -o-transform 400ms ease;
            transition: transform 400ms ease;
    -webkit-transition-property: -webkit-transform, left, right; 
    -webkit-backface-visibility: hidden; 
}

.sb-hide { 
    display: none; 
}