.profilebar, #profilemenu {
    nav ul li{
        & a:before {
            border-radius: 50%;
            background: $main;
            padding: 7px;
            color: $white;
            margin-right: 10px;
            vertical-align: middle;
        }
        &:last-child a {
            border-bottom: 0;
        }
        &.accountmanagement a{
            @include icon(user-plus);
        }
        &.account a{
            @include icon(cog);
        }
        &.archived_vacancies a, &.my-archived_vacancies a {
            @include icon(archive);
        }
        &.candidates a, &.my-candidates a{ 
            @include icon(profile);
        }
        &.dashboard a{
            @include icon(home);
        }
        &.forum a{
            @include icon(forum);
        }
        &.jobalerts a{
            @include icon(bell);
        }
        &.faq a{
            @include icon(question);
        }
        &.logout a{
            @include icon(exit);
        }
        &.activities a, &.my-activities a {
            @include icon(clipboard);
        }
        &.profile-and-company a{
            @include icon(building);
        }
        &.profile-and-cv a{
            @include icon(user);
        }
        &.subscriptions a, &.my-subscriptions a {
            @include icon(clipboard);
        }
        &.vacancies a, &.my-vacancies a{
            @include icon(file-text);
        }
        &.volunteer a, &.my-volunteer a{
            @include icon(ribbon);
        }
        &.webwinkel a{
            @include icon(gift);
        }
    }
}

.logintip {
    position: absolute;
    background: $smarttip;
    color: $smarttiptext;
    right: 10px;
    top: 71px;
    line-height: normal;
    padding: 10px 16px 16px;
    box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.2);
    display: none;
    max-width: 200px;
    z-index: 99;
    a {
        display: block;
        color: $smarttiptext;
        text-align: right;
        padding: 6px 0;
        font-size: 16px;
        &:before {
            content:'\2715';
            display: inline-block;
        }
    }
    &:after {
        bottom: 100%;
        left: 17%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-color: rgba(136, 183, 213, 0);
        border-bottom-color: $smarttip;
        border-width: 10px;
        margin-left: -10px;
    }
}

section#header {
    position: relative;
    padding-top: 77px;
    #topbar.sticky-scrolled {

    }
    #topbar {
        position: fixed;
        width: 100%;
        top: 0;
        z-index: 100;
        .profilebar {
            display: none;
            .usermenu {
                .photo {
                    display: inline-block;
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    vertical-align: middle;
                    margin-left: 13px;
                }
            }
        }
        #headerbar {
            background: $white;
            box-shadow: 0px 0px 8px rgba(0,0,0,0.3);
            #logo {
                @media print {
                    display: none !important;
                }
                width: 30%;
                height: 77px;
                text-align: center;
                line-height: 77px;
                display: inline-block;
                float: left;
                a {
                    display: block;
                    padding: 0 0px;
                    overflow: hidden;
                    height: 77px;
                    text-align: left;
                    img {
                        max-width: none;
                        width: 175px;
                        margin-left: -73px;
                        @include transition();
                    }
                }
            }
            header {
                background: $white;
                @include grid-row();
                height: 77px;
                text-align: right;
                nav {
                    @media print {
                        display: none !important;
                    }
                    display: none;
                    ul {
                        list-style-type: none;
                        margin: 0;
                        padding: 0;
                        li {
                            a {
                                display: block;
                                color: $navigationLink;
                                text-decoration: none;
                                font-weight: 700;
                                font-size: 15px;
                            }
                            &.active {
                                a {
                                    color: $main;
                                    .submenu {
                                        li.active {
                                            a {
                                                color: $white;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .mobilemenubtn {
                    display: block;
                    float: right;
                    line-height: 77px;
                    text-align: center;
                    .profilebar, .searchBtn {
                        width: 70px;
                        display: inline-block;
                        position: relative;
                        font-size: 0rem;
                        height: 77px;
                        vertical-align: top;
                    }
                    .profilebar .photo {
                        margin-left: 0;
                    }
                    .searchBtn {
                        @include icon("search"){
                            font-size: 22px;
                            color: $main;
                        }
                    }
                    @import "hamburger-icon";
                }

                @import "searchform";
            }
        }
    }
}

@media #{$medium-up} {
    section#header {
        #topbar {
            #headerbar {
                header {
                    #logo {
                        width: 50%;
                        a {
                            img {
                                @include transition();
                                max-height: 100%;
                                margin-left: 0;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media #{$large-up} {
    .profilebar, #profilemenu {
        nav ul li{
            &.logout a{
                &:before{
                    background: $error;
                };
            }
        }
    }
    section#header {
        padding-top: 133px;
        &.noProfilebar {
            padding-top: 98px;
        }
        #topbar {
            background: $background;
            -webkit-backface-visibility: hidden;
            &.sticky-scrolled {
                #headerbar {
                    #logo {
                        height: 75px;
                        line-height: 75px;
                        @include transition();
                    }
                    nav {
                        li {
                            a {
                                line-height: 75px;
                                height: 75px;
                            }
                            .submenu {
                                ul {
                                    li {
                                        a {
                                            height: auto;
                                            line-height: inherit;
                                        }
                                    }
                                }
                            }
                        }
                        &.actions {
                            & > ul > li > a {
                                height: 75px;
                                &.searchBtn{
                                    line-height: 35px;
                                }
                            }
                        }
                    }
                }
            }
            .logintip {
                right: 8px;
                top: 53px;
                &:after {
                    left: 91%;
                }
            }
            .profilebar {
                @include clearfix;
                display: block;
                text-align: right;
                position: relative;
                .profilebar-element  {
                    padding: 5px 20px 5px 20px;
                    display: inline-block;
                    vertical-align: top;
                    margin-right: 6px;
                    border-left: 1px solid darken($navigationBorder,10);
                    font-weight: 700;
                    color: darken($background,40);
                    span {
                        display: inline-block;
                        vertical-align: middle;
                    }
                    a {
                        color: darken($background,40);
                    }
                }
                .languageswitcher {
                    ul {
                        margin: 0;
                        line-height: inherit;
                        li {
                            color: darken($navigationBorder,40);
                            display: inline-block;
                            vertical-align: middle;
                            margin-right: 10px;
                            &:last-child {
                                margin-right: 0;
                            }
                            a {
                                &.active {
                                    font-weight: 700;
                                    color: $main;
                                }
                                &:hover {
                                    text-decoration: underline;
                                }
                            }
                        }
                    }
                }
                .usermenu {
                    margin-right: 40px;
                    text-align: left;
                    &:hover {
                        nav {
                            top: 52px;
                            @include transition($transition-time: 400ms);
                        }
                    }
                    a,span.username {
                        color: $main;
                        display: inline-block;
                        vertical-align: middle;
                        margin-right: 10px;
                    }
                    a {
                        font-size: 14px;
                        font-weight: 500;
                    }
                    a.register:before {
                        content: "";
                        display: inline-block;
                        border-right: 1px solid;
                        height: 14px;
                        margin-right: 10px;
                        vertical-align: middle;
                    }
                    .photo {
                        position: absolute;
                        right:18px;
                        top: 3px;
                    }
                    nav {
                        position: absolute;
                        background: $white;
                        border: 1px solid $navigationBorder;
                        z-index: 99;
                        top: -500px;
                        right: 8px;
                        box-shadow: 2px 2px 4px rgba(0,0,0,0.1);
                        @include transition($transition-time: 400ms);
                        &:before {
                            content: "";
                            display: block;
                            height: 20px;
                            position: absolute;
                            top: -20px;
                            width: 100%;
                        }
                        ul {
                            list-style-type: none;
                            margin: 0;
                            &:after, &:before {
                                bottom: 100%;
                                left: 90%;
                                border: solid transparent;
                                content: " ";
                                height: 0;
                                width: 0;
                                position: absolute;
                                pointer-events: none;
                            }

                            &:after {
                                width: 0;
                                height: 0;
                                border-style: solid;
                                border-width: 0 9px 12px 9px;
                                border-color: transparent transparent $white transparent;
                            }
                            &:before {
                                margin-left: -1px;
                                width: 0;
                                height: 0;
                                border-style: solid;
                                border-width: 0 10px 13px 10px;
                                border-color: transparent transparent $navigationBorder transparent;
                            }
                            li {
                                a {
                                    padding: 10px 85px 10px 20px;
                                    display: block;
                                    margin-right: 0;
                                    border-bottom: 1px solid $navigationBorder;
                                    color: $text;
                                    &:hover {
                                        background: lighten($navigationBorder,5);
                                    }
                                }
                                &.search {
                                    display: none;
                                }
                                &:first-child{
                                    &:hover {
                                        &:before {
                                            bottom: 100%;
                                            left: 90%;
                                            border: solid transparent;
                                            content: " ";
                                            height: 0;
                                            width: 0;
                                            position: absolute;
                                            pointer-events: none;
                                            border-style: solid;
                                            border-width: 0 9px 12px 9px;
                                            border-color: transparent transparent lighten($navigationBorder,5) transparent;
                                            z-index: 1;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            #notificationbar.show {
                & + #headerbar .actions {
                    top: 53px;
                }
            }
            #headerbar {
                box-shadow: 0px 5px 12px -3px rgba(0, 0, 0, 0.2);
                border-top: 1px solid darken($navigationBorder,10);
                header {
                    height: auto;
                    float: none;
                    #logo {
                        width: 100%;
                        max-width: 170px;
                        height: 97px;
                        line-height: 97px;
                        @include transition();
                        a {
                            display: block;
                            height: 100%;
                            img {
                                width: 100%;
                            }
                        }
                    }
                    nav {
                        .submenu {
                            width: 100%;
                            position: absolute;
                            left: 0;
                            display: none;
                            padding: 20px;
                            background: $submenu;
                            box-shadow: inset 0px 5px 16px rgba(0,0,0,0.2);
                            z-index: 80;
                            ul {
                                position: relative;
                                li {
                                    display: block;
                                    a {
                                        height: auto;
                                        line-height: inherit;
                                        padding: 10px 0;
                                        @include transition();
                                        color: $submenuNavigationLink;
                                    }
                                    &:hover a{
                                        border-bottom: 0;
                                        color: lighten($submenuNavigationLinkHover,20);
                                        @include transition();
                                    }
                                }
                            }
                        }
                        display: block;
                        @include grid-row();
                        text-align: center;
                        & > ul {
                            display: inline-block;
                        }
                        & > ul > li {
                            display: inline-block;
                            vertical-align: top;
                            & > a {
                                padding: 0 0 0 24px;
                                line-height: 97px;
                                height: 97px;
                                border-bottom: 0px solid;
                                @include transition();
                                &:after {
                                    content: "";
                                    border-right: 1px solid $navigationBorder;
                                    display: inline-block;
                                    height: 38px;
                                    vertical-align: middle;
                                    padding-left: 24px;
                                }
                            }
                            &.hassub {
                                &:hover {
                                    > .submenu {
                                        // display: block;
                                    }
                                }
                                &.active {
                                    > a {
                                        color: $main;
                                    }
                                }
                            }
                            &:hover {
                                > a {
                                    border-bottom: 5px solid $main;
                                    @include transition();
                                }
                            }
                            &:last-child a {
                                padding-right: 0;
                                &:after {
                                    display: none;
                                }
                            }
                            &:first-child a {
                                padding-left: 0;
                            }
                            .submenu {
                                ul {
                                    left: 0;
                                    li {
                                        a {
                                            text-align: left;
                                        }
                                    }
                                }
                            }
                        }
                        li.search {
                            a {
                                font-size: 0rem;
                                @include icon(search){
                                    font-size: 14px;
                                    font-weight: 700;
                                };
                            }
                            &:hover {
                                a {
                                    border-bottom: 0;
                                    color: $searchIconDesktop;
                                }
                            }
                        }
                        &.actions {
                            position: absolute;
                            display: block;
                            right: 0;
                            top: 1px;
                            width: auto;
                            & > ul {
                                display: inline-block;
                                & > li {
                                    border-left: 1px solid darken($white, 10);
                                    position: relative;
                                    padding: 0 10px;
                                    display: inline-block;
                                    float: left;
                                    & > a {
                                        height: 97px;
                                        @include transition();
                                        span.account {
                                            display: inline-block;
                                            width: 35px;
                                            height: 35px;
                                            border-radius: 50%;
                                            border: 2px solid $background;
                                            background: $white no-repeat center center;
                                            @include translate(0,-50%);
                                            top: 50%;
                                            position: relative;
                                            @include transition(border);
                                        }
                                    }
                                    .name {
                                        display: none;
                                    }
                                    .submenu {
                                        width: 400%;
                                        right: 0;
                                        left: inherit;
                                        border-radius: 0 0 0 5px;
                                        ul {
                                            li {
                                                text-align: right;
                                                padding: 0;
                                            }
                                        }
                                    }
                                    &:hover {
                                        & > a {
                                            border-bottom: 0;
                                            span.account {
                                                border-color: $main;
                                            }
                                        }
                                        .submenu {
                                            height: auto;
                                            opacity: 1;
                                            padding: 20px;
                                            @include transition();
                                            ul {
                                                display: block;
                                                li {
                                                    border-left: 0;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .mobilemenubtn {
                        display: none;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 1025px) and (max-width: 1200px) {
    section#header {
        #topbar {
            #headerbar {
                header {
                    #logo {
                        max-width: 133px;
                        a {
                            img {
                                width: 100%;
                            }
                        }
                    }
                    nav {
                        ul li {
                            a {
                                padding: 0 15px 0 0;
                                font-size: 14px;
                                &:before {
                                    padding-right: 15px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media #{$xlarge-up} {
    section#header {
        #notificationbar {
        }
        #logo {
        }
        header {
            nav {
                &.eportfolio,
                &.employers {
                    padding-left: 0;
                }
            }
        }
        #topbar #headerbar header nav.actions {
            & > ul {
                display: inline-block;
                & > li {
                    padding: 0 25px;
                }
            }
        }
    }
}