.breadcrumbs {
    @include grid-row();
    .wrapper {
        padding-top: 7px;
        padding-bottom: 7px;
        @include grid-column(12);
        &.breadcrumbwrapper {
            @media print {
                display: none !important;
            }
        }
        ul.nav_breadcrumbs {
            list-style: none;
            margin-top: 10px;
            margin-bottom: 10px;
            margin-left: 0;
              font-size: 14px;
            li {
                display: inline-block;
                a {
                    color: $breadcrumbsNonactive;
                }
                &:last-child a {
                    color: $breadcrumbsActive;
                }
            }
            span.divider {
                color: $breadcrumbsDevider;
                margin-left: 15px;
                margin-right: 15px;
            }
        }
        #filter {
            ul.nav_breadcrumbs {
                margin-left: 0;
                margin-top: 0;
            }
        }
    }
}

