#wrapper div#main section {
    div.articles {
        width: 100%;
        color: $text;
        border-bottom: 1px solid $border;
        &.articles_employers-resetpassword {
            display: none;
        }
        &.articles_my-applications {
            .my_applications_contentblock {
                .my-applications {
                    display: block;
                }
                .richtext {
                    padding-right: 1.07143rem;
                    width: 100%;
                    float: left;
                    border: 1px solid #eff1c0;
                    background-color: #fffeec;
                    background-position: 18px 18px;
                    background-repeat: no-repeat;
                    background-image: url("/images/notice_icon.png");
                    padding: 15px;
                    padding-left: 55px;
                    margin-top: 10px;
                    min-height: 25px;
                    height: 100%;
                    color: #535353;
                    margin-left: 0;
                }
            }
        }
        &.articles_cv {
            .cv_print_or_download {
                .contentblockcontent {
                    &.title {
                        h2 {
                            color: $white;
                        }
                    }
                    &.richtext {
                        color: $white;
                    } 
                    a {
                        &.downloadcv,
                        &.printcv {
                            color: $white;
                            font-weight: bold;
                        }
                    }
                }
            }
        }
        div.article_wrapper {
            @include grid-row();
            h1 {
                color: $main;
                font-size: 24px;
            }
        }
        .requestedarticle {
            background: $white;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            div.article_wrapper {
                max-width: 924px;
            }
            & ~ .article {
                .card {
                    width: 100%;
                    margin: $column-gutter auto;
                    max-width: 924px;
                    @include clearfix;
                    p {
                        line-height: 34px;
                        margin-top: -7px;
                    }
                    h2 {
                        font-size: 1.2rem;
                    }
                    header + img {
                        margin-right: 20px;
                        float: left;
                    }
                    a.btn {
                        float: right;
                    }
                }
            }
        }
        &.articles_home {
            .requestedarticle {
                background: none;
                div.article_wrapper {
                    max-width: inherit;
                }
            }
        }
        .article_formintro {
            background: $articleintro;
            .article_wrapper {
                .firstcb {
                    margin: 20px 0;
                    .title {
                        .toggler {
                            @include icon-after('arrow-down') {
                                color: $white;
                                font-size: 35px;
                                position: absolute;
                                right: 50px;
                                top: 0;
                                cursor: pointer;
                                    transition: all 200ms ease-in-out;
                            }
                            &.open {
                                &:after {
                                    transform: scaleY(-1);
                                }
                            }
                        }
                        h2 {
                            color: $articleintroText;
                            text-transform: initial;
                            font-size: 30px;
                            text-shadow: 2px 2px $shadow;
                        }
                    }
                    .text {
                        p {
                            color: $articleintroText;
                        }
                    }
                    .richtext {
                        display: inline-block;
                    }
                }
            }
            &.requestedarticle {
                div.article_wrapper {
                    max-width: 100%;
                }
            }
        }
        .article_accordion {
            background-color: $white;
            .contentblock:nth-child(2) {
                margin: 50px 0;
            }
            .contentblock {
                padding-top: 10px;
                margin: $column-gutter/2 0;
            }
            .contentblock.lastcb {
                margin-bottom: 70px;
            }
            .contentblockcontent.title {
                h2 {
                    color: $main;
                }
                &.accordion-toggle {
                    cursor: pointer;
                    @include lightbtn($color: $border, $icon:0);
                    width: 100%;
                    padding-left: 10px;
                    border-width: 1px;
                    display: block;
                    h2 {
                        margin: 5px;
                        padding: 6px;
                        color: $main;
                        font-size: 1.1rem;
                    }
                    &:hover,
                        &.show {
                        background-color: $main;
                        border-color: $main;
                        h2 {
                            color: $white;
                        }
                    }
                    &.hide {
                        display: block;
                    }
                }
            }

            .accordion-content {
                padding: 6px;
            }

            .contentblockcontent.richtext {
                color: $text;
                a {
                    font-weight: bold;
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
        &.articlesrequested {
            .article_news {
                h1 {
                    display: none;
                }
            }
        }
        &.news-overview {
            .article_wrapper {
                padding-bottom: 36px;
                border-bottom: 1px solid #DEDEDE;
                .article_news {
                    .contentblocks {
                        .contentblockcontent.richtext {
                            color: $text;
                        }
                    }
                }
            }
        }
        &.articlesoverview {
            #overview {
                @include grid-row();
                position: relative;
            }
            .article {
                .card {
                    width: 100%;
                    @include clearfix;
                }
                a p{
                    color: $text;
                    margin-bottom: 0;
                    line-height: 34px;
                }
                p {
                    line-height: 34px;
                    margin-top: -7px;
                }
                h2 {
                    font-size: 1.2rem;
                }
                header + img {
                    margin-right: 20px;
                    float: left;
                }
                a.btn {
                    float: right;
                }
                &.article_news:first-child {
                    .articlemaincontentblocks {
                        padding: 0;
                    }
                    h2 {
                        font-size: 1.5rem;
                    }
                }
            }
        }
    }
}

@media #{$medium-up} {
    #wrapper div#main section {
        div.articles {
            &.articlesoverview {
                @include clearfix();
                .article_news {
                    @include grid-column(6);
                    float: right;
                    clear: right;
                    &:first-child {
                        float: left;
                        clear: none;
                        .photocontainer {
                            width: 100%;
                            padding: 0;
                        }
                        .richtext {
                            width: 100%;
                            padding: 0;
                        }
                    }
                }
            }
            .article_formintro {
                .article_wrapper {
                    .firstcb {
                        .title {
                            float: left;
                            width: 25%;
                        }
                        .richtext {
                            width: 60%;
                        }
                    }
                }
            }
        }
    }
}

@media #{$large-up} {

}