#wrapper div#main section {
    .pager {
        text-align: right;
        > a,
        li {
            display: inline-block;
        }
        a {
            @include lightbtn();
            overflow: inherit;
            display: inline-block;
            margin-right: 10px;
            font-weight: bold;
            vertical-align: top;
            border: 0;
            color: $pagerText;
            background-color: $pager;
            &:hover,
            &.active {
                background-color: $pagerActive;
                color: $pagerActiveText;
            }
            &:before {
                font-size: 24px;
            }
        }
        .previous a{
            @include icon(arrow-left);
        }
        .next a{
            padding: 0 55px 0 40px;
            @include icon(arrow-right){
                right: 32px;
            };
        }

        ul {
            display: inline-block;
            padding: 0px;
            margin: 0px;
            li {
                list-style-type: none;
            }
        }
        .pagenumbers {
            a {
                padding: 0 12px;
            }
        }
        a.nextpage, a.previouspage {
            padding: 0 16px;
            font-size: 0rem;
            &:before {
                margin-left: 0;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
            }
        }
        a.previouspage{
            @include icon(arrow-left);
        }
        a.nextpage{
            @include icon(arrow-right);
        }
    }

    .requestedpager {
        float: none;
        margin-top: 0;
        margin-right: 0;
        display: block;
        text-align: left;
        @include clearfix();
        .link_wrapper {
            display: inline-block;
        }
        div.previous, div.next {
            @include grid-column(2);
            padding: 0;
            background: lighten($border,5);
            a {
                font-size: 0rem;
                border: 0;
                margin: 0;
                display: block;
                text-align: center;
                padding: 0;
                height: 60px;
                line-height: 60px;
                border-radius: 0;
                &:hover {
                    background: $main;
                }
                &:before {
                    font-size: 39px;
                }
            }
        }
        div.next a:before {
            right: inherit;
        }
        div.overview {
            @include grid-column(8);
            padding: 0;
            background: $white;
            a {
                border: 0;
                margin: 0;
                display: block;
                text-align: center;
                padding: 0;
                height: 60px;
                line-height: 60px;
                border-radius: 0;
            }
        }
    }

    .module-container.mode-uniquerequested .requestedpager {
        border-bottom: 1px solid $border;
        div.previous, div.next {
            background: lighten($border,5);
            a {
                &:before {
                    color: $module-main;
                }
                &:hover {
                    &:before {
                        color: $white;
                    }
                    background: $module-main;
                }
            }
        }
        div.overview {
            a {
                color: $module-main;
                &:hover {
                    color: $white;
                    background: $module-main;
                }
            }
        }
    }
    
}

@media #{$large-up} {
    #wrapper div#main section {
        .requestedpager {
            div.previous, div.next {
                @include grid-column(1);
                padding: 0;
            }
            div.overview {
                @include grid-column(10);
                padding: 0;
            }
        }
    }
}