& {
    background: $background;
    &.login {
        background-image: $loginBg;
        background-repeat: no-repeat;
        background-size: cover;
        .login_divider {
            color: $text;
            position: relative;
            display: block;
            text-align: center;
            &:before {
                border-top: 1px solid $border;
                content: "";
                margin: 0 auto;
                position: absolute;
                top: 50%;
                left: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                z-index: -1;
            }
            span {
                background: $white;
                padding: 0 15px;
            }
        }
        .form_user_login {
            .login_divider {
                margin-top: 25px;
                margin-bottom: 25px;
            }
        }
    }
}

@import "header";

#wrapper {
    min-height:100%;
    position:relative;

    &.theme-1 {
        div#main section .brandbox {
            background-color: $theme1;
        }
    }
    &.theme-2 {
        div#main section .brandbox {
            background-color: $theme2;
        }
    }
    &.theme-3 {
        div#main section .brandbox {
            background-color: $theme3;
        }
    }
    &.theme-4 {
        div#main section .brandbox {
            background-color: $theme4;
        }
    }
    .social_media_channels {
        @media (max-width: 700px) {
            width: 100%;
            margin-top: 20px;
        }
        @media (max-width: 500px) {

        }
        a {
            padding-left: 5px;
            padding-right: 5px;
            span {
                display: inline-block;
                width: 30px;
                height: 30px;
                @include transition();
                &.icon_facebook {
                    @include icon("facebook");
                }
                &.icon_twitter {
                    @include icon("twitter");
                }
                &.icon_youtube {
                    @include icon("youtube");
                }
                &.icon_linkedin {
                    @include icon("linkedin");
                }
                &:hover {
                    @include transform(translate(0px, 0px) scale(1.1));
                }
            }
        }
    }
    div#main {
        section {

            @import "brandbox";

            h2.empty_title {
                display: none;
            }
            div.breadcrumbs {
                &.login,
                &.resetpassword,
                &.resetpassword-email-unknown,
                &.resetpassword-email-sent,
                &.employers-login,
                &.employers-resetpassword,
                &.employers-resetpassword-email-unknown,
                &.employers-resetpassword-email-sent,
                &.dashboard {
                    display: none;
                }
                &.jobalertform {
                    span.article_title,
                    span.article_title_divider {
                        display: none;
                    }
                }
            }

            .wrapper {
                .inner {
                    padding: 0 $column-gutter/2;
                }
            }

            div.employer {
                div.company_overview {
                    padding-top: 20px;
                    padding-bottom: 20px;
                    @include grid-row();
                    div.company_fields {
                        @include grid-column(12);
                        padding-left: 0;
                        div.actions {
                            clear: both;
                        }
                        div.fieldsgroup_vacancy {
                            .row_wrapper {
                                position: relative;
                                background: $white;
                                .title {
                                    display: none;
                                }
                            }
                            span.bottom {
                                display: block;
                                margin: 15px 0;
                                vertical-align: middle;
                                .formcontainer {
                                    background: none;
                                }
                            }
                            .value.applications {
                                background: $vacancyBottombar;
                                @include clearfix;
                            }
                            .bottom.editing_blocked {
                                .edit, .delete {
                                    @include btn();
                                    padding: 6px 10px;
                                    font-size: 0rem;
                                    margin-right: 0;
                                    vertical-align: top;
                                    &:before {
                                        font-size: 16px;
                                        vertical-align: middle;
                                    }
                                }
                                .edit {
                                    @include icon(edit);
                                    margin-right: 10px;
                                }
                                .delete {
                                    @include icon(delete);
                                }
                            }
                        }
                        div.fieldsgroup_vacancy {
                            background: transparent;
                            border: none;
                            .row_wrapper {
                                border: 1px solid #CECECE;
                                @include transition();
                                display: block;
                                margin-left: $column-gutter/2;
                                margin-bottom: $column-gutter;
                                a {
                                    color: $text;
                                }
                                .bottom.applications {
                                    a {
                                        @include btn();
                                    }
                                }
                                .value,
                                .fieldsgroup {
                                    padding: $column-gutter/2;
                                    .photo {
                                        display: none;
                                    }
                                    &.top,
                                    &.fieldsgroup_top {
                                        background-color: $main;
                                        color: $white;
                                        font-size: 1.2rem;
                                        margin: 0;
                                        font-weight: bold;
                                        span,
                                        .field {
                                            display: inline-block;
                                        }
                                        span.title,
                                        .title_field {
                                            line-height: 1em;
                                            height: 1em;
                                            width: 60%;
                                            overflow: hidden;
                                            border: none;
                                            a {
                                                color: $white;
                                            }
                                            .wrapper {
                                                padding: 0;
                                                h2 {
                                                    margin: 0;
                                                    color: $white;
                                                }
                                            }
                                        }
                                        span.creationdate,
                                        .duration_field {
                                            text-align: right;
                                            float: right;
                                            width: 40%;
                                            .wrapper {
                                                padding: 0;
                                                .value {
                                                    padding: 0;
                                                    width: 100%;
                                                    line-height: 35px;
                                                }
                                            }
                                        }
                                    }
                                    &.fieldsgroup_center {
                                        background-color: $white;
                                        min-height: 91px;
                                        .field {
                                            .wrapper {
                                                padding: 0;
                                                .value {
                                                    padding: 0;
                                                    width: 100%;
                                                    line-height: 20px;
                                                }
                                            }
                                        }
                                    }
                                    &.bottom,
                                    &.fieldsgroup_bottom {
                                        color: $text;
                                        border: none;
                                        background-color: transparent;
                                        a {
                                            color: $text;
                                            background-color: transparent;
                                            border: none;
                                        }
                                        .actions {
                                            text-align: right;
                                            a {
                                                color: darken($main, 10);
                                            }
                                            a.edit {
                                                margin-right: 3%;
                                            }
                                        }
                                        .field {
                                            display: inline-block;
                                            border: none;
                                            .wrapper {
                                                padding: 0;
                                                .value {
                                                    padding: 0;
                                                    width: 100%;
                                                }
                                            }
                                            &.amountpositions_field {
                                                padding-right: 5px;
                                                .value {
                                                    padding: 0;
                                                    display: inline-block;
                                                    padding-right: 5px;
                                                }
                                                .label {
                                                    display: inline-block;
                                                }
                                            }
                                            &.tariff_field {
                                                vertical-align: top;
                                                .value {
                                                    line-height: 20px;
                                                    padding-left: 5px;
                                                    border-left: 1px solid #666;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            div.candidates {
                padding-top: 20px;
                padding-bottom: 20px;
                @include grid-row();
                div.profile {
                    @include grid-column(12);
                }
                div.profiledetails {
                    div.link_wrapper {
                        display: inline-block;
                        margin-right: 30px;
                    }
                }
            }
            .scrollCompanion {
                background-color: $white;
                border-bottom: 1px solid $border;
                padding: 15px 0;
                .wrapper {
                    @include grid-row();
                    h1 {
                        font-size: 1.8rem;
                        @include grid-column(12);
                        float: none;
                        span {
                            font-weight: normal;
                            font-size: 1.5rem;
                        }
                    }
                }
            }
            div.rij1_wrapper {
                width: 100%;
                background: $white;
                padding: 20px 0;
                border-bottom: 1px solid #DEDEDE;
                div.contentblocks {
                    @include grid-row();
                    div.profile_completeness {
                        @include grid-column(12);
                    }
                    div.contentblock.text {
                        @include grid-column(12);
                    }
                }
            }
            #overview {
                #favorite_wrapper {
                    border-right: none;
                }
            }
            #favorite_wrapper {
                height: 100%;
                display: inline-block;
                position: absolute;
                top: 28%;
                margin-bottom: 0px;
                span.favorite {
                    a.favorite {
                        span.star {
                            height: 100%;
                            vertical-align: top;
                            @include icon('star-empty');
                            &:hover {
                                @include icon('star-full');
                            }
                            &.starred {
                                @include icon('star-full');
                                &:hover {
                                    @include icon('star-empty');
                                }
                            }
                        }
                    }
                }
            }
        }

        .fieldsgroup .title,
        .title-wrapper h2 {
            position: relative;
            a.group-scroller,
            a.form-scroller {
                left: 0px;
                top: -150px;
                position: absolute;
            }
        }
        #loading {
            position: absolute;
            z-index: 10;
            background-color: $loadingloaderBackground;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            span.loader{
                position: fixed;
                display: block;
                top: 32%;
                left: 50%;
                transform: translate(-50%,-50%);
                z-index: 99;
                &:before{
                    width:20px;
                    height:20px;
                    border-radius:20px;
                    content:'';
                    position:absolute;
                    background:$loadingloaderBlock1;
                    animation: preloader_before 1.5s infinite ease-in-out;
                }
                &:after{
                    width:20px;
                    height:20px;
                    border-radius:20px;
                    content:'';
                    position:absolute;
                    background:$loadingloaderBlock2;
                    left:22px;
                    animation: preloader_after 1.5s infinite ease-in-out;
                }
            }
        }
    }
}

@keyframes preloader_before {
    0% {transform: translateX(0px) rotate(0deg)}
    50% {transform: translateX(50px) scale(1.2) rotate(260deg); background:$loadingloaderBlock2;border-radius:0px;}
    100% {transform: translateX(0px) rotate(0deg)}
}
@keyframes preloader_after {
    0% {transform: translateX(0px)}
    50% {transform: translateX(-50px) scale(1.2) rotate(-260deg);background:$loadingloaderBlock1;border-radius:0px;}
    100% {transform: translateX(0px)}
}