// Quick fixes for aeroplane menu height
div.aeroplane_bar ul.aeroplane_menu ul.aeroplane_submenu li, div.aeroplane_contextbutton ul.aeroplane_submenu li {
    height: 1.3em !important;
}
div.aeroplane_gui .dijitContentPane h1, div.aeroplane_gui .dijitTabPane h1.dijitContentPane {
    height: 37px !important;
}
div.aeroplane_gui select {
    width: auto;
}
div.aeroplane_databaseeditor_table form div.pos_formelement {
    height: 25px !important;
}
div.aeroplane_wjgroup_contentcollector div.aeroplane_wjtext_index_name {
    display: none;
}
div.aeroplane_gui p, div.aeroplane_gui label {
    margin: 0;
}
&.aeroplanelucid #aeroplane_topbar{
    & + .aeroplane_sitemaincontent section#header #topbar {
        top: 37px;
    }
    .article-contentblocks {
        &.article-contentblocks {
            .articlemaincontentblocks {
                .photo {
                     .photocontainer {
                        visibility: visible;
                    }
                }
                .photophoto {
                    .photocontainer {
                        visibility: visible;
                    }
                }
                .phototext, .textphoto {
                    .photocontainer {
                        visibility: visible;
                    }
                }
            }
        }
    }
}