#wrapper div#main section .searchform_container {
    background: $main;

    .search {
        padding-bottom: 54px;
        padding-top: 36px;
        padding-left: $column-gutter/2;
        padding-right: $column-gutter/2;
        h1{
            color: $white;
            font-size: 24px;
        }
        h2{

        }
        .formcontainer {
            background: transparent;
            form.searchform_form {
                position: relative;
                .queryfield {
                    padding: 0;
                    width: 80%;
                    label {
                        display: none;
                    }
                    input {
                        color: $white;
                        font-size: 1.5rem;
                        height: 4rem;
                        border-bottom: 1px solid $white;
                    }
                    .bar:before, .bar:after {
                        background: $white;
                    }
                }
                .typefield {
                    display: none;
                }
                .submit_field {
                    position: absolute;
                    right: 0;
                    top: 0;
                    height: 100%;
                    width: 20%!important;
                    margin: 0 !important;
                    .formactions {
                        button {
                            position: relative;
                            width: 100%;
                            height: 55px;
                            border-style: none;
                            transition: none;
                            margin-left: 0!important;
                            padding: 0;
                            font-size: 0;
                            @include icon("search"){
                                font-size: 1rem;
                                position: absolute;
                                top: 50%;
                                left:50%;
                                transform: translate(-50%, -50%);
                            }
                            &:before {
                                margin-left: 0;
                            }
                            &:active{
                                box-shadow: none;
                            }
                        }
                    }
                }
            }
        }
    }
}
.searchresults {
    padding-top: 55px;
    padding-bottom: 55px;
    padding-left: $column-gutter/2;
    padding-right: $column-gutter/2;

    > h2 {
        font-size: 2.5rem;
        margin-bottom: 20px;
    }
    .card {
        padding: 0;
        .photo_field {
            display: none;
        }
        .title_field {
            padding: $column-gutter/2;
            background: $main;
            h2{
                font-size: 16px;
                margin: 0;
            }
            a{
                color: $white;
            }
        }
        .label {
            color: $white;
            float: right;
        }
        .description_field {
            display: inline-block;
            width: 100%;
            p{
                padding: $column-gutter/2;
                margin: 0;
            }
            .morelink {
                display: block;
                padding: $column-gutter/2;
                text-align: right;
                color: $text;
                background: #ededed;
            }
        }

    }
}
@media #{$medium-up} {
    #wrapper div#main section .searchform_container {
        .search {
            .formcontainer {
                form.searchform_form {
                    .submit_field {
                        .formactions {
                            button {
                                font-size: 1rem;
                                &:before {
                                    left: 20%;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}